<template>
  <div id="app">
    <router-view></router-view>
  </div>
</template>

<script>
import { common } from "@/assets/js/request.js";
export default {
  name: "app",
  data() {
    return {
      hotelId: this.$store.state.hotelId
    };
  },
  mounted() {
    common.hotelInfo({
      Suffix: this.hotelId,
      fn: data => {
        this.$store.commit("storeHotel", data);
      }
    });
  }
};
</script>

<style>

</style>



