'use strict'
import axios from 'axios'
import Qs from 'qs'

import { Toast } from 'mint-ui';
// Vue.use(Toast)
// axios 配置https://api.douban.com/v2/movie/in_theaters
// axios.defaults.baseURL = 'http://192.168.3.37';
// axios.defaults.baseURL = 'http://192.168.3.47:8080';
//响应时间
axios.defaults.timeout = 5000;
//`withCredentails`选项表明了是否是跨域请求
axios.defaults.withCredentials = false;
//设置默认请求头
axios.defaults.headers = {
    'X-Requested-With': 'XMLHttpRequest',
    "Content-Type": "application/json"
};
//添加请求拦截器
axios.interceptors.request.use(config => {
    // 判断是否存在token，如果存在的话，则每个http header都加上token
    if (sessionStorage.getItem('token') != undefined) {
        config.headers.token = sessionStorage.getItem('token');
    }
    return config
}, error => {
    return Promise.reject(error)
})

//成功的处理错误的回调函数
function checkStatus(response) {
    // 异常状态下，可以弹出一个错误提示，告诉用户
    // Message.error(response.data.msg, '111111111');
    Toast(response.data.msg)
}
//失败的回调函数
function checkCode(res) {
    // 如果code异常(这里已经包括网络错误，服务器错误，可以弹出一个错误提示，告诉用户
    // Message.error('网络异常');
    // Message.error(res);
    // console.log(res.Error, res)
    Toast('网络异常')
}
export default {
    post(obj) {
        return axios.post(obj.url, obj.params).then(res => {
            if (res.data.code == 0) {
                if (obj.fn) {
                    obj.fn(res.data.data)
                }
            } else {
                return checkStatus(res)
            }
        }
        ).catch(err => {
            return checkCode(err)
        }
            )
    },
    get(obj) {
        return axios.get(obj.url, {
            params: obj.params
        }).then(res => {
            if (res.data.code == 0) {
                if (obj.fn) {
                    obj.fn(res.data.data)
                }
            } else {
                return checkStatus(res)
            }
        }
            ).catch(err => {
                return checkCode(err)
            }
            )
    },
    delete(obj) {
        return axios.delete(obj.url, {
            data: obj.params,
        }).then(res => {
            if (res.data.code == 0) {
                if (obj.fn) {
                    obj.fn(res.data.data)
                }
            } else {
                return checkStatus(res)
            }
        }
            ).catch(err => {
                return checkCode(err)
            }
            )
    },
}



