import $http from "./http.js"
//公共模块
const common = {
    // 查询 房间订单列表
    hotelInfo(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
}
//定房模块
const roomBooking = {
    // 查询 全部活动列表
    activitylist(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //首页房型列表
    roomCategory(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 查询 具体活动列表
    specificActivity(obj) {
        return $http.get({
            url: "/mobile/78CDF1/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    }
}
//订餐模块
const foodBooking = {
    // 查询 菜单列表
    foodlist(obj) {
        return $http.get({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 保存订餐的订单
    foodsettlement(obj) {
        return $http.post({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    //查询订餐的订单详情
    foodOrderDetails(obj) {
        return $http.get({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    },
    // 保存订餐的订单
    foodpay(obj) {
        return $http.post({
            url: "/mobile/78CDF1/food/" + obj.Suffix,
            params: obj.params,
            fn: obj.fn
        })
    }
}

export {
    common,
    roomBooking,
    foodBooking
}


