import Vue from 'vue'
import Vuex from 'vuex'
import axios from 'axios'
Vue.use(Vuex)

const state = {
    hotelId: 2,
    HotelInfo: ''
}

const mutations = {
    // 缓存酒店信息
    storeHotel(state, Info) {
        state.HotelInfo = Info
    },
}

export default new Vuex.Store({
    state,
    mutations
})



